    <div class="collaboImg">
      <RouterLink to="/wearingeul/">
        <img :src="require(`@/assets/img/collabo${collaboNum}.jpg`)" />
      </RouterLink>
    </div>
    <div class="collaboTitle">
      <router-link to="/wearingeul/">{{ collaboTitle }}</router-link>
      <router-link to="/wearingeul/">{{ collaboCompany }}</router-link>
    </div>
  </div>
  <div class="collaboBottom">
