//     if (to.fullPath !== "/login" && !userStore.isLoggedIn) {
//         next("/login")
//     } else if (to.fullPath == "/login" && userStore.isLoggedIn) {
//         next("/")
//     } else {
//         next();
//     }
// })

export default createRouter({
  history: createWebHistory(),
  routes,
  // loginStore,
});
