<template>
  <listmenu-compont
    titleMenu="딥펜/만년필"
    titleLink="/wearingeul/dip-pen"
    :listMenu="deppenList"
    @itemSelected="filterGoods"
  />

  <div class="mainContent listContent">
    <div class="mainContentTop spaceBetween">
      <select name="" id="">
        <option value="new" selected>등록순</option>
        <option value="best">인기순</option>
      </select>
    </div>
    <main-content-component
      :goodsList="
        filteredGoods.slice(pageStartIdx, pageStartIdx + ITEM_PER_PAGE)
      "
      object="deppen-product"
    />
    <pagination-component
      :list="filteredGoods"
      v-bind="{ ITEM_PER_PAGE, PAGE_PER_SECTION }"
      @change-page="onChangePage"
    />
  </div>
</template>

<script>
import { computed, ref } from "vue";
import { deppenList } from "@/assets/js/listMenu/deppen";
import { deppenGoods } from "@/assets/js/goodsList/deppen";
import MainContentComponent from "@/components/MainContentComponent.vue";
import ListmenuCompont from "@/components/ListmenuCompont.vue";
import PaginationComponent from "@/components/PaginationComponent.vue";

export default {
  name: "DippenListView",
  components: { MainContentComponent, ListmenuCompont, PaginationComponent },
  data() {
    return {
      deppenGoods: deppenGoods,
      deppenList: deppenList,
      curPage: 1,
    };
  },
  setup() {
    const filteredGoods = ref(deppenGoods);

    const ITEM_PER_PAGE = ref(9);
    const PAGE_PER_SECTION = ref(5);
    let curPage = ref(1);

    // 페이지 시작 인덱스
    const pageStartIdx = computed(() => {
      return (curPage.value - 1) * ITEM_PER_PAGE.value;
    });

    // 페이지 변경
    const onChangePage = (data) => {
      curPage.value = data;
    };

    // 필터 메소드
    const filterGoods = (selectedItem) => {
      if (!selectedItem) {
        filteredGoods.value = deppenGoods;
      } else {
        filteredGoods.value = deppenGoods.filter(
          (item) => item.category === selectedItem
        );
      }
      onChangePage(1);
    };

    return {
      ITEM_PER_PAGE,
      PAGE_PER_SECTION,
      filteredGoods,
      pageStartIdx,
      //
      onChangePage,
      filterGoods,
    };
  },
};
</script>
